import React, {useEffect, useState} from 'react';
import logo from './logo.svg';
import './App.css';
import Pages from "./pages";

function App() {
  const [user,setUser] = useState(null);
  useEffect(()=> {
    fetch("https://gitconnected.com/v1/portfolio/oguzcanbudumlu")
        .then(res => res.json())
        .then(user => {
          setUser(user);
        })
  }, []);

  if(!user){
    return <div/>;
  }

  return (
      <Pages user={user} />
  );
}

export default App;
