import React from 'react';
import Layout from "../../components/Layout";
import {Paragraph, SectionTitle} from "../../styles";
import {Activities, Degree, EducationItem, Grade, Institution} from "./styles";


const Education = ({user}) => {
    return(
        <Layout user={user}>
            <div>
                <SectionTitle>Education</SectionTitle>
                <ul>
                    {user.education.map((education,i) => (
                        <EducationItem key={i}>
                            <Institution>{education.position}</Institution>
                            <div>
                                <Institution>{education.institution}</Institution>
                                <Degree>
                                    {education.studyType} {education.area}
                                </Degree>
                                {' '}
                                <span>&sdot;</span>
                                {' '}
                                <Grade>
                                    {education.gpa}
                                </Grade>
                                {' '}
                                <span>&sdot;</span>
                                {' '}
                                <span>
                                    {education.start.year} to {education.end.year}
                                </span>


                            </div>
                            <Paragraph>{education.description}</Paragraph>
                            <Activities>Activities</Activities>
                            <Paragraph>{education.activities}</Paragraph>
                        </EducationItem>
                    ))}
                </ul>
            </div>
        </Layout>
    )

}

export default Education;


